import { Controller } from '@ali/4ever-cangjie';
import findNativeTextArea from '../utils/findNativeTextArea';

export default function onCangjieFocus(controller: Controller, next: any) {
  const isPreviewing = controller.query('isQuickPreview');
  if (isPreviewing && controller.value.isBlurred) {
    // 额外调用一次 native blur 事件，避免键盘弹出、数据没有 focus 的情形
    const textArea = findNativeTextArea(controller);
    if (textArea?.blur) {
      textArea.blur();
    }
    return controller;
  }
  return next();
}
